<template>
	<div class="addEdit-block" :style='{"padding":"30px 0","fontSize":"14px","color":"#999","background":"#f6f6f6"}'>
		<el-form
			:style='{"border":"0px solid rgba(255,255,255,1)","padding":"30px","borderRadius":"4px","alignItems":"flex-start","flexWrap":"wrap","background":"rgba(255,255,255,0)","display":"flex","fontSize":"inherit"}'
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="150px"
		>
			<template >
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="" prop="yishenggonghao">
					<el-input v-model="ruleForm.yishenggonghao" placeholder="" clearable  :readonly="ro.yishenggonghao"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="" prop="yishenggonghao">
					<el-input v-model="ruleForm.yishenggonghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="" prop="yishengxingming">
					<el-input v-model="ruleForm.yishengxingming" placeholder="" clearable  :readonly="ro.yishengxingming"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="" prop="yishengxingming">
					<el-input v-model="ruleForm.yishengxingming" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="" prop="yishengxingbie">
					<el-input v-model="ruleForm.yishengxingbie" placeholder="" clearable  :readonly="ro.yishengxingbie"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="" prop="yishengxingbie">
					<el-input v-model="ruleForm.yishengxingbie" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="" prop="lianxishouji">
					<el-input v-model="ruleForm.lianxishouji" placeholder="" clearable  :readonly="ro.lianxishouji"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="" prop="lianxishouji">
					<el-input v-model="ruleForm.lianxishouji" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="upload" v-if="type!='info' && !ro.touxiang" label="" prop="touxiang">
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
						@change="touxiangUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="upload" v-else-if="ruleForm.touxiang" label="" prop="touxiang">
					<img v-if="ruleForm.touxiang.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.touxiang.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.touxiang.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="select" v-if="type!='info'"  label="" prop="zhiwei">
					<el-select :disabled="ro.zhiwei" v-model="ruleForm.zhiwei" placeholder="" >
						<el-option
							v-for="(item,index) in zhiweiOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="" prop="zhiwei">
					<el-input v-model="ruleForm.zhiwei"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="" prop="yuhao">
					<el-input v-model.number="ruleForm.yuhao" placeholder="" clearable  :readonly="ro.yuhao"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="" prop="yuhao">
					<el-input v-model="ruleForm.yuhao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="select" v-if="type!='info'"  label="" prop="keshimingcheng">
					<el-select :disabled="ro.keshimingcheng" v-model="ruleForm.keshimingcheng" placeholder="" >
						<el-option
							v-for="(item,index) in keshimingchengOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="" prop="keshimingcheng">
					<el-input v-model="ruleForm.keshimingcheng"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="" prop="shanzhangleixing">
					<el-input v-model="ruleForm.shanzhangleixing" placeholder="" clearable  :readonly="ro.shanzhangleixing"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="" prop="shanzhangleixing">
					<el-input v-model="ruleForm.shanzhangleixing" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="input" v-if="type!='info'"  label="" prop="jiuzhenshijian">
					<el-input v-model="ruleForm.jiuzhenshijian" placeholder="" clearable  :readonly="ro.jiuzhenshijian"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="" prop="jiuzhenshijian">
					<el-input v-model="ruleForm.jiuzhenshijian" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' class="textarea" v-if="type!='info'" label="" prop="yishengjianjie">
					<el-input
					  style="min-width: 200px; max-width: 600px;"
					  type="textarea"
					  :rows="8"
					  placeholder=""
					  v-model="ruleForm.yishengjianjie" >
					</el-input>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else-if="ruleForm.yishengjianjie" label="" prop="yishengjianjie">
					<span :style='{"fontSize":"14px","lineHeight":"40px","color":"inherit","background":"none","fontWeight":"500","display":"inline-block"}'>{{ruleForm.yishengjianjie}}</span>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-if="type!='info'"  label="" prop="rongyuxinxi">
					<editor 
						style="min-width: 200px; max-width: 600px;"
						v-model="ruleForm.rongyuxinxi" 
						class="editor" 
						action="file/upload">
					</editor>
				</el-form-item>
				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else-if="ruleForm.rongyuxinxi" label="" prop="rongyuxinxi">
                    <span :style='{"fontSize":"14px","lineHeight":"40px","color":"inherit","background":"none","fontWeight":"500","display":"inline-block"}' v-html="ruleForm.rongyuxinxi"></span>
                </el-form-item>
			<el-form-item :style='{"padding":"0","margin":"30px 0","alignItems":"center","textAlign":"center","display":"flex","width":"100%","perspective":"320px","-webkitPerspective":"320px","fontSize":"48px","justifyContent":"flex-start"}' class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-tijiao16" :style='{"margin":"0 2px","fontSize":"18px","color":"inherit","display":"none"}'></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-quxiao09" :style='{"margin":"0 2px","fontSize":"18px","color":"inherit","display":"none"}'></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-fanhui01" :style='{"margin":"0 2px","fontSize":"18px","color":"inherit","display":"none"}'></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
	data() {
		let self = this
		var validateIdCard = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!checkIdCard(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateUrl = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isURL(value)) {
				callback(new Error("URL"));
			} else {
				callback();
			}
		};
		var validateMobile = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isMobile(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validatePhone = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isPhone(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateEmail = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isEmail(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				yishenggonghao : false,
				yishengxingming : false,
				yishengxingbie : false,
				lianxishouji : false,
				touxiang : false,
				zhiwei : false,
				yuhao : false,
				keshimingcheng : false,
				shanzhangleixing : false,
				jiuzhenshijian : false,
				yishengjianjie : false,
				rongyuxinxi : false,
				clicktime : false,
				storeupnum : false,
			},
			
			
			ruleForm: {
				yishenggonghao: '',
				yishengxingming: '',
				yishengxingbie: '',
				lianxishouji: '',
				touxiang: '',
				zhiwei: '',
				yuhao: '',
				keshimingcheng: '',
				shanzhangleixing: '',
				jiuzhenshijian: '',
				yishengjianjie: '',
				rongyuxinxi: '',
				clicktime: '',
			},
		
			zhiweiOptions: [],
			keshimingchengOptions: [],

			
			rules: {
				yishenggonghao: [
				],
				yishengxingming: [
				],
				yishengxingbie: [
				],
				lianxishouji: [
				],
				touxiang: [
				],
				zhiwei: [
					{ required: true, message: '', trigger: 'blur' },
				],
				yuhao: [
					{ required: true, message: '', trigger: 'blur' },
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				keshimingcheng: [
					{ required: true, message: '', trigger: 'blur' },
				],
				shanzhangleixing: [
				],
				jiuzhenshijian: [
				],
				yishengjianjie: [
				],
				rongyuxinxi: [
				],
				clicktime: [
				],
				storeupnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
	},
	methods: {
		
		// 
		download(file){
			window.open(`${file}`)
		},
		// 
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='yishenggonghao'){
							this.ruleForm.yishenggonghao = obj[o];
							this.ro.yishenggonghao = true;
							continue;
						}
						if(o=='yishengxingming'){
							this.ruleForm.yishengxingming = obj[o];
							this.ro.yishengxingming = true;
							continue;
						}
						if(o=='yishengxingbie'){
							this.ruleForm.yishengxingbie = obj[o];
							this.ro.yishengxingbie = true;
							continue;
						}
						if(o=='lianxishouji'){
							this.ruleForm.lianxishouji = obj[o];
							this.ro.lianxishouji = true;
							continue;
						}
						if(o=='touxiang'){
							this.ruleForm.touxiang = obj[o];
							this.ro.touxiang = true;
							continue;
						}
						if(o=='zhiwei'){
							this.ruleForm.zhiwei = obj[o];
							this.ro.zhiwei = true;
							continue;
						}
						if(o=='yuhao'){
							this.ruleForm.yuhao = obj[o];
							this.ro.yuhao = true;
							continue;
						}
						if(o=='keshimingcheng'){
							this.ruleForm.keshimingcheng = obj[o];
							this.ro.keshimingcheng = true;
							continue;
						}
						if(o=='shanzhangleixing'){
							this.ruleForm.shanzhangleixing = obj[o];
							this.ro.shanzhangleixing = true;
							continue;
						}
						if(o=='jiuzhenshijian'){
							this.ruleForm.jiuzhenshijian = obj[o];
							this.ro.jiuzhenshijian = true;
							continue;
						}
						if(o=='yishengjianjie'){
							this.ruleForm.yishengjianjie = obj[o];
							this.ro.yishengjianjie = true;
							continue;
						}
						if(o=='rongyuxinxi'){
							this.ruleForm.rongyuxinxi = obj[o];
							this.ro.rongyuxinxi = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
				}
			}
			// 
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					var json = data.data;
					if(((json.yishenggonghao!=''&&json.yishenggonghao) || json.yishenggonghao==0) && this.$storage.get("role")!=""){
						this.ruleForm.yishenggonghao = json.yishenggonghao
						this.ro.yishenggonghao = true;
					}
					if(((json.yishengxingming!=''&&json.yishengxingming) || json.yishengxingming==0) && this.$storage.get("role")!=""){
						this.ruleForm.yishengxingming = json.yishengxingming
						this.ro.yishengxingming = true;
					}
					if(((json.yishengxingbie!=''&&json.yishengxingbie) || json.yishengxingbie==0) && this.$storage.get("role")!=""){
						this.ruleForm.yishengxingbie = json.yishengxingbie
						this.ro.yishengxingbie = true;
					}
					if(((json.lianxishouji!=''&&json.lianxishouji) || json.lianxishouji==0) && this.$storage.get("role")!=""){
						this.ruleForm.lianxishouji = json.lianxishouji
						this.ro.lianxishouji = true;
					}
					if(((json.touxiang!=''&&json.touxiang) || json.touxiang==0) && this.$storage.get("role")!=""){
						this.ruleForm.touxiang = json.touxiang
						this.ro.touxiang = true;
					}
				} else {
					this.$message.error(data.msg);
				}
			});
            this.zhiweiOptions = ",,".split(',')
            this.$http({
				url: `option/keshixinxi/keshimingcheng`,
				method: "get"
            }).then(({ data }) => {
				if (data && data.code === 0) {
					this.keshimingchengOptions = data.data;
				} else {
					this.$message.error(data.msg);
				}
            });
			
		},
    // 

    info(id) {
      this.$http({
        url: `jiuzhenxinxi/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //
        let reg=new RegExp('../../../upload','g')//g
        this.ruleForm.rongyuxinxi = this.ruleForm.rongyuxinxi.replace(reg,'../../../python96xeny5v/upload');
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 
    onSubmit() {
	if(this.ruleForm.touxiang!=null) {
		this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
	}
var objcross = this.$storage.getObj('crossObj');
      //
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
		this.$refs["ruleForm"].validate(valid => {
			if (valid) {
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid = crossuserid;
					this.ruleForm.crossrefid = crossrefid;
					let params = { 
						page: 1, 
						limit: 10, 
						crossuserid:this.ruleForm.crossuserid,
						crossrefid:this.ruleForm.crossrefid,
					} 
				this.$http({ 
					url: "jiuzhenxinxi/page", 
					method: "get", 
					params: params 
				}).then(({ 
					data 
				}) => { 
					if (data && data.code === 0) { 
						if(data.data.total>=crossoptnum) {
							this.$message.error(this.$storage.get('tips'));
							return false;
						} else {
							this.$http({
								url: `jiuzhenxinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.jiuzhenxinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});

						}
					} else { 
				} 
			});
		} else {
			this.$http({
				url: `jiuzhenxinxi/${!this.ruleForm.id ? "save" : "update"}`,
				method: "post",
			   data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.parent.showFlag = true;
							this.parent.addOrUpdateFlag = false;
							this.parent.jiuzhenxinxiCrossAddOrUpdateFlag = false;
							this.parent.search();
							this.parent.contentStyleChange();
						}
					});
				} else {
					this.$message.error(data.msg);
			   }
			});
		 }
         }
       });
    },
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.jiuzhenxinxiCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
